.approach {
    padding: 86px 0 94px;
    background-color: #f3f3f3;

    .title {
        @media (max-width: 900px) {
            margin-bottom: 35px;
        }
    }

    @media (max-width: 985px) {
        padding-top: 25px;
        padding-bottom: 25px;
    }

    &__list {
        margin: 0;
        padding: 0;
        counter-reset: count;
    }

    &__item {
        margin-bottom: 40px;
        border-radius: 5px;
        box-shadow: -1px -1px 10px 1px rgba(214, 214, 214, 1);
        color: #424242;
        background: #fff;

        &:nth-child(2n) {
            display: flex;
            flex-direction: row-reverse;
            text-align: right;

            @media (max-width: 660px) {
                display: block;
            }

            .approach__text {
                padding: 70px 90px 70px 33px;

                @media (max-width: 985px) {
                    padding: 25px 25px;
                }
            }

            .approach__step {
                right: auto;
                left: -52px;
            }            
        }
    }

    &__image {
        position: relative;
        max-width: 340px;
        max-height: 236px;
        display: inline-block;
        vertical-align: top;

        @media (max-width: 985px) {
            max-width: calc(340px/1.5);
            max-height: calc(236px/1.5);
        }

        @media (max-width: 660px) {
            margin: auto;
            display: block;
        }
    }

    &__picture {
        display: block;
        width: 100%;
        height: 100%;

        @media (max-width: 660px) {
            padding-top: 25px;
            margin-bottom: 25px;
        }
    }

    &__text {
        display: inline-block;
        vertical-align: top;
        width: 64%;
        padding: 70px 33px 55px 88px;
        font-size: 0.875rem;

        @media (max-width: 985px) {
            padding: 25px 15px 25px 25px;
        }
        
        @media (max-width: 660px) {
            width: 100%;
            text-align: center;
        }
    }

    &__step {
        @include centerer-y();

        right: -53px;
        width: 103px;
        height: 103px;
        padding-top: 10px;
        border-radius: 5px;
        border: 2px solid #fff;
        text-align: center;
        font-size: 1.25rem;
        background-color: #de2b24;
        color: #fff;

        &:before {
            content: counter(count);
            counter-increment: count;
            display: block;
            font-family: $ff-steps;
            font-size: 3rem;
            line-height: 1;
            color: #fff;
        }

        @media (max-width: 985px) {
            display: none;;
        }
    }

    &__title {
        margin-bottom: 6px;
        font-size: 1.1rem;
        font-family: $ff-main-bold;
        padding-right: 17px;      
    }

    &__description {
        margin: 0;
        line-height: 1.5;
    }
}